@import url(https://fonts.googleapis.com/css2?family=Monoton&display=swap);
/****logo styling*********/
.logo {
  font-family: "Monoton", cursive;
  font-weight: lighter;
}
/*********nav styling******/
nav ul {
  list-style-type: none;
}
a:link {
  text-decoration: none;
  margin: 5px;
}

a.nav-link {
  color: #007bff !important;
}
/*********hero banner video********/
.bgVideo {
  width: 100vw;
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
}
div.row {
  height: 10vh;
}

/*********content********/

h2 {
  margin-bottom: 1rem;
}

/***Footer styling****/
footer {
  margin-top: 10rem;
}
/*********recipelist styling********/

/***********recipe card styling*******/
.recipecard {
  display: block;
  background-color: #f3c654;
  box-shadow: 0 0 5px #333;
  padding: 1rem;
  margin: 1rem;
  border-radius: 4px;
  text-align: left;
  -webkit-align-items: center;
          align-items: center;
  min-height: 300px;
  padding: 15px;
}
.recipecard p {
  margin: 5px;
}
.recipecard ul {
  margin: 5px;
}
hr {
  margin-top: 10px;
  margin-bottom: 10px;
}
h3 {
  text-align: center;
  margin-bottom: 1rem;
}
.recipecard img {
  width: 200px;
}
.recipeImg {
  text-align: center;
}
.recipelistlist {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1rem;
}
button {
  margin: 1rem;
  padding: 1rem;
  background-color: burlywood;
  font-family: "Comfortaa", cursive;
  border-radius: 4px;
}
input {
  margin: 1rem;
  padding: 5px;
  font-family: "Comfortaa", cursive;
}
textarea {
  margin: 1rem;
  padding: 5px;
}
button a {
  color: white;
}
button a:hover {
  color: rgb(255, 255, 255);
  text-decoration: none;
}
/******form styling**********/

form div {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-style: dotted;
  border-color: #007bff;
  border-radius: 4px;
  margin: 0.5rem;
}
div.form-group {
  border-style: none;
}
div.col {
  border-style: none;
}
div.row {
  border-style: none;
}
section {
  border-style: dotted;
  border-color: #007bff;
  border-radius: 4px;
  margin: 0.5rem;
  padding: 10px;
}

label {
  margin: 1rem;
  padding: 5px;
}
.newRecipePage {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
.view {
  text-align: end;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-end;
          align-items: flex-end;
  -webkit-align-items: center;
          align-items: center;
}
ul {
  text-align: left;
}

button,
input,
optgroup,
select,
textarea {
  margin: 10px !important;
}
textarea {
  width: 40vw;
}
/*******popup*********/
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.1);
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.popup {
  position: relative;
  top: 0;
  left: 0;
  height: 50%;
  width: 50%;
  background-color: white;
  padding: 2rem;
  box-shadow: 0 0 5px #333;
  margin: 0 auto;
}

button:hover {
  background-color: blanchedalmond;
  border: #333 3px solid;
}

* {
  margin: 0;
  box-sizing: border-box;
  font-family: "Comfortaa", cursive;
}
body {
  background-color: #f5e8c5;
  margin-top: 1rem;
  margin-left: 10vw;
  margin-right: 10vw;
  padding-top: 2rem;
  line-height: 1.6;
  background-image: url("https://source.unsplash.com/LSph4t5At1k");
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: center;
  background-size: cover;
}
body.background {
  opacity: 0.5;
}
.header {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 2rem;
}

nav ul {
  list-style-type: none;
}
a:link {
  text-decoration: none;
}

nav li {
  display: inline-block;
}
.main {
  margin-top: 5vh;
}

.active {
  font-weight: bold;
}

.strikethrough {
  text-decoration: line-through;
}
.bg {
  background-color: rgba(255, 255, 255, 0.8);
}
.navbar-light .navbar-brand {
  color: #007bff;
}

